<template>
    <svg class="circlePacking" width="800" height="800"></svg>
</template>

<style>
    .circlePacking{
        background-color: transparent;
    }
    /* 图内的style样式 */
    .node {
    cursor: pointer;
    }

    .node:hover {
    stroke: #000;
    stroke-width: 1.5px;
    }

    .node--leaf {
    fill: white;
    }
    .label {
    font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
    text-anchor: middle;
    /* text-shadow: rgb(255, 255, 255) 1px 0 10px; */
    fill:rgb(250, 253, 251);
    font-weight:bolder;
    /* text-shadow: 0 1px 0 #fff, 1px 0 0 #fff, -1px 0 0 #fff, 0 -1px 0 #fff; */
    }

    .label,
    .node--root,
    .node--leaf {
    pointer-events: none;
    }
</style>

<script>
    import * as d3 from "../../../public/d3.v3.min.js";
    export default{
        mounted(){
            var circleFill = function(d) {
                if (d['color']) {
                    return d.color;
                } else if(!d.parent){
                    return "#00000000";
                }else{
                    return d.children ? color(8-Math.sqrt(d.r)) : "#facbefb0";
                }
            }
            var calculateTextFontSize = function(d) {
            return Math.min(2 * d.r, (2 * d.r - 8) / this.getComputedTextLength() * 11) + "px";
            }

            var margin = 20,
            diameter = 480;
            var color = d3.scale.linear()
                .domain([0, 8])
                // .range(["hsl(0,100%,50%)", "black"])
                .range(["#7400bf", "#ff7bac"])
                .interpolate(d3.interpolateHcl);

            var pack = d3.layout.pack()
                .padding(2)
                .size([diameter - margin, diameter - margin])
                .value(function(d) {
                    return d.size;
                })

            var svg = d3.select(".circlePacking")
            .append("g")
            .attr("transform", "translate(" + diameter / 2 + "," + diameter / 2 + ")");


            d3.json("./target1.json", function(error, root) {
            var focus = root,
            nodes = pack.nodes(root),
            view;
            console.log(nodes)
            var circle = svg.selectAll("circle")
            .data(nodes)
            .enter().append("circle")
            .attr("class", function(d) {
                return d.parent ? d.children ? "node" : "node node--leaf" : "node node--root";
            })
            .style("fill", circleFill)
            .on("click", function(d) {
                if (focus !== d) zoom(d), d3.event.stopPropagation();
            });

            circle.append("svg:title")
            .text(function(d) {
                return d.name;
            })

            var text = svg.selectAll("text")
            .data(nodes)
            .enter().append("text")
            .attr("class", "label")
            .style("color", "blue")
            .style("fill-opacity", function(d) {
                return (d.parent === root &&d.r>5)? 1 : 0;
            })
            .style("display", function(d) {
                return d.parent === root ? null : "none";
            })
            .text(function(d) {
                return d.name;
            })
            .style("font-size", calculateTextFontSize)
            .attr("dy", ".35em");

            var node = svg.selectAll("circle,text");

            d3.select("body")
            .style("background", color(-1))
            .on("click", function() {
                zoom(root);
            });

            zoomTo([root.x, root.y, root.r * 2 + margin]);

            function zoom(d) {
            var focus0 = focus;
            focus = d;

            var transition = d3.transition()
                .duration(d3.event.altKey ? 7500 : 750)
                .tween("zoom", function(d) {
                var i = d3.interpolateZoom(view, [focus.x, focus.y, focus.r * 2 + margin]);
                return function(t) {
                    zoomTo(i(t));
                };
                });

            transition.selectAll("text")
                .filter(function(d) {
                return d.parent === focus || this.style.display === "inline";
                })
                .style("fill-opacity", function(d) {
                return (d.parent === focus &&d.r>5)? 1 : 0;
                })
                .each("start", function(d) {
                if (d.parent === focus) this.style.display = "inline";
                })
                .each("end", function(d) {
                if (d.parent !== focus) this.style.display = "none";
                });
            }

            function zoomTo(v) {
            var k = diameter / v[2];
            view = v;
            node.attr("transform", function(d) {
                return "translate(" + (d.x - v[0]) * k + "," + (d.y - v[1]) * k + ")";
            });
            circle.attr("r", function(d) {
                return d.r * k;
            });
            }

            d3.select(self.frameElement).style("height", diameter + "px");
            });
        }
    }
</script>