import { createRouter, createWebHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue'

const routes = [
  {
    path: '/',
    name: 'home',
    component:HomeView,
    meta:{
      title: 'HOME'
    }
  },
  {
    path: '/submission',
    name:'submission',
    component:() =>import('../views/SubmissionInterfaceView.vue'),
    children:[
      {
        path:'',
        component: () =>import('../views/submisson/TestSubmisson.vue')
      },
      {
        path:'reportList',
        component: () =>import('../views/submisson/ReportListView.vue')
      }
    ],
    meta:{
      title: '2控制台提交'
    }
  },
  {
    path: '/SBOM',
    name:'SBOM',
    component:() =>import('../views/SBOMSubmissionInterfaceView.vue'),
    children:[
      {
        path:'transform',
        component: () =>import('../views/SBOM/SBOMtransform.vue'),
        meta:{
          title: '4.1SBOM转换'
        }
      },
      {
        path:'compare',
        component: () =>import('../views/SBOM/SBOMcompare.vue'),
        meta:{
          title: '4.2SBOM比较'
        }
      },
      {
        path:'test',
        component: () =>import('../views/SBOM/SBOMtest.vue')
      },
    ]
  },
  {
    path: '/test',
    name:'test',
    component: () =>import('../views/SBOM/SBOMtest.vue'),
    children:[
      {
        path:'SBOMc',
        component: () =>import('../views/SBOM/SBOMtest.vue'),
        meta:{
          title: '4.2.1SBOM比较报告'
        }
      },
    ]
  },
  {
    path: '/bigscreen',
    name:'bigscreen',
    component:() =>import('../views/bigscreen.vue'),
  },
  {
    path: '/bigscreen2',
    name:'bigscreen2',
    component:() =>import('../views/bigscreen2.vue'),
    meta:{
      title: '1漏洞感知大屏'
    }
  },
  {
    path: '/restler',
    name:'restler',
    component:() =>import('../views/restler.vue'),
    meta:{
      title: '3程序分析'
    }
  },
  {
    path:'/FUZZanalyse',
    name:'FUZZanalyse',
    component: () =>import('../views/FUZZ/FUZZanalyse.vue'),
    meta:{
      title: '3.1程序分析报告'
    }
  },
  {
    path: '/console',
    name: 'console',
    component:() =>import('../views/ConsoleView.vue'),
    children:[
      {
        path:'overview',
        component: () =>import('../views/vva/Overview.vue'),
        meta:{
          title: '2.1 总览'
        }
      },
      {
        path:'detail',
        component: () =>import('../views/vva/Details.vue'),
        meta:{
          title: '2.2详情'
        }
      }
    ]
  },
  {
    path: '/login',
    name: 'login',
    component: () => import('../views/Login.vue'),
  },
  {
    path: '/register',
    name: 'register',
    component: () => import('../views/Register.vue'),
  },
  {
    path: '/account',
    name: 'account',
    component: () => import('../views/AccountView.vue'),
  },
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
